import {
  createEditor,
  createMergeTable,
  createMergeTable0,
  expectIsCollapsedInCell,
  getTableFromController,
} from '../helpers';

describe('table/commands/insertRow', () => {
  it('插入行后，光标落在新插入行的第一个单元格', async () => {
    const { controller } = await createEditor({
      table: createMergeTable(),
      tableSelection: {
        startRowIndex: 1,
        endRowIndex: 1,
        startColIndex: 0,
        endColIndex: 3,
      },
      shouldAdjust: false,
    });
    const table = getTableFromController(controller);
    controller.command('insertRow', table.nodes[1], 'insert-row-below');
    expectIsCollapsedInCell(controller, 2, 0);
  });

  it('插入行后，光标落在新插入行的第一个非合并单元格', async () => {
    const { controller } = await createEditor({
      table: createMergeTable0(),
      tableSelection: {
        startRowIndex: 1,
        endRowIndex: 1,
        startColIndex: 0,
        endColIndex: 3,
      },
      shouldAdjust: false,
    });
    const table = getTableFromController(controller);
    controller.command('insertRow', table.nodes[1], 'insert-row-above');
    expectIsCollapsedInCell(controller, 1, 2);
  });
});
